// import axios from "axios";
import React, { Component } from "react";
import { SiMicrosoftexcel } from "react-icons/si";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  PermutationsStore,
  FC_GetPermutationsByDistrict,
  FC_GetNationalPermutations,
} from "../../actions";
import { FC_GetDistricts } from "../../actions/transfer.action";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Loading from "../../components/Loading/Loading";
// import { APP_URL } from "../../config/app.config";
import { StoreState } from "../../reducers";
import { DistrictTypes } from "../../types/TransferTypes";

interface PermutationUserInfo {
  user_id: string;
  first_name: string | null;
  middle_name: string | null;
  last_name: string | null;
  full_name: string;
  staff_code: string;
  sex: string;
  dob: string;
  marital_status: string;
  nid: string;
  email: string;
  phone_numbers: string;
}

interface PermutationSchoolInfo {
  school_name: string;
  school_code: string;
}

export interface GetPermutations {
  role_id: string;
  user_one: string;
  user_two: string;
  position_code: string;
  school_one: string;
  school_two: string;
  academic_year_id: string;
  reb_user_id: string;
  qualification_id: string;
  role: string;
  position_name: string;
  qualification_name: string;
  permutation_status: string;
  reb_status: string;
  usersInfo: PermutationUserInfo[];
  schools: PermutationSchoolInfo[];
}

interface PermutationsReportsProps {
  auth: Auth;
  basic: Basic;
  permutations: PermutationsStore;
  FC_GetPermutationsByDistrict: (
    district_code: string,
    page: number,
    limit: number,
    setLoading: (status: boolean) => void
  ) => void;
  FC_GetNationalPermutations: (
    page: number,
    limit: number,
    setLoading: (status: boolean) => void
  ) => void;
}
interface PermutationsReportsState {
  loading: boolean;
  selected_school_code: string | null;
  selected_district_code: string | null;
  districts: DistrictTypes[] | null;
  page: number;
  limit: number;
  schools_selected: PermutationSchoolInfo[];
}

class _PermutationsReports extends Component<
  PermutationsReportsProps,
  PermutationsReportsState
> {
  constructor(props: PermutationsReportsProps) {
    super(props);

    this.state = {
      loading: false,
      districts: null,
      selected_district_code:
        this.props.auth.district !== null &&
        this.props.auth.role !== null &&
        this.props.auth.role.role_id === "3"
          ? this.props.auth.district.district_code
          : null,
      selected_school_code: null,
      page: 1,
      limit: 1000,
      schools_selected: [],
    };
  }

  FilteredData = () => {
    let test = this.props.permutations.permutations?.find(
      (itm) => itm.page === this.state.page
    );
    if (test !== undefined) {
      for (let item of test.permutations_data) {
        this.setState({
          schools_selected: [...this.state.schools_selected, ...item.schools],
        });
      }
      return test.permutations_data;
    } else {
      return null;
    }
  };

  componentDidMount = () => {
    if (this.state.districts === null) {
      this.setState({ loading: true });
      FC_GetDistricts(
        (status: boolean, data: DistrictTypes[] | null, msg: string) => {
          console.log({ data });

          if (data !== null) {
            this.setState({
              districts: data,
              loading: false,
            });
          }
          this.setState({ loading: false });
        }
      );
    }
    // Permutations by district
    if (
      this.props.permutations.permutations === null &&
      this.props.auth.district !== null &&
      this.props.auth.role !== null &&
      this.props.auth.role.role_id === "3"
    ) {
      // DDE
      this.props.FC_GetPermutationsByDistrict(
        this.props.auth.district.district_code,
        this.state.page,
        this.state.limit,
        this.setLoading
      );
    }

    // // National level
    if (
      this.props.auth.role?.role_id === "4" &&
      this.props.permutations.permutations === null
    ) {
      // this.setState({ loading: true });
      this.props.FC_GetNationalPermutations(
        this.state.page,
        this.state.limit,
        (status: boolean) => {
          this.setState({ loading: status });
        }
      );
    }
  };

  FindUser = (
    user_id: string,
    users: PermutationUserInfo[]
  ): PermutationUserInfo | null => {
    let user = users.find((itm) => itm.user_id === user_id);
    if (user !== undefined) {
      return user;
    } else {
      return null;
    }
  };

  FindSchool = (school_code: string, schools: PermutationSchoolInfo[]) => {
    let school = schools.find((itm) => itm.school_code === school_code);
    if (school !== undefined) {
      return school;
    } else {
      return null;
    }
  };

  setLoading = (status: boolean) => this.setState({ loading: status });
  render() {
    if (this.state.districts === null || this.state.loading === true) {
      return (
        <DashboardContainer>
          <div className="flex flex-col items-center justify-center gap-3 w-full text-center py-5">
            <Loading />
            <div>Loading permutations...</div>
          </div>
        </DashboardContainer>
      );
    }
    return (
      <DashboardContainer>
        <div>
          <div className="flex flex-row items-center justify-between gap-3">
            <div className="text-3xl font-bold flex flex-col">
              <span>Permutations report</span>
              <span className="text-base font-light text-gray-600">
                List of permutations
              </span>
            </div>
            <div className="flex flex-row items-center gap-3">
              {this.props.auth.district !== null &&
              this.props.auth.role !== null &&
              this.props.auth.role.role_id === "3" ? (
                <div className="flex flex-col rounded px-3 py-2 bg-gray-100 text-center">
                  <span className="text-gray-500">Selected district</span>
                  <span className="font-extrabold">
                    {this.props.auth.district.district_name}
                  </span>
                </div>
              ) : (
                this.props.auth.role !== null &&
                this.props.auth.role.role_id === "4" && (
                  <div className="flex flex-col">
                    <span>Filter district</span>
                    <select
                      value={
                        this.state.selected_district_code === null
                          ? ""
                          : this.state.selected_district_code
                      }
                      onChange={(e) =>
                        this.setState({
                          selected_district_code: e.target.value,
                        })
                      }
                      className="border border-gray-500 rounded px-3 py-2"
                    >
                      <option value="">Choose district</option>
                      {this.state.districts.map((item, i) => (
                        <option key={i + 1} value={item.district_code}>
                          {item.district_name}
                        </option>
                      ))}
                    </select>
                  </div>
                )
              )}
              {this.props.auth.school !== null &&
              this.props.auth.role !== null &&
              this.props.auth.role.role_id === "2" ? (
                <div className="flex flex-col rounded px-3 py-2 bg-gray-100 text-center">
                  <span className="text-gray-500">Selected school</span>
                  <span className="font-extrabold">
                    {this.props.auth.school.school_name}
                  </span>
                </div>
              ) : (
                <div className="flex flex-col">
                  <span>Filter School</span>
                  <select className="px-3 py-2 rounded border border-gray-500">
                    <option value="">Choose school</option>
                    {this.state.schools_selected.map((item, i) => (
                      <option value={item.school_code}>
                        {item.school_name}
                      </option>
                    ))}
                  </select>
                </div>
              )}
            </div>
          </div>
          <div className="flex flex-row items-center w-full mt-2 gap-3">
            <input
              placeholder="Search"
              className="px-3 py-2 rounded border border-gray-500 w-full"
            />
            <div className="bg-yellow-100 text-black hover:bg-accent-900 hover:text-white rounded px-3 py-2 flex flex-row items-center gap-2 cursor-pointer">
              <div>
                <SiMicrosoftexcel className="text-xl" />
              </div>
              <span>Export</span>
            </div>
          </div>
        </div>
        <div className="mt-5">
          <table className="w-full rounded">
            <thead>
              <tr className="bg-blue-100 rounded-t">
                <td className="px-3 py-2 text-left">#</td>
                <td className="px-3 py-2 text-left">User one</td>
                <td className="px-3 py-2 text-left">School one</td>
                <td className="px-3 py-2 text-left">User two</td>
                <td className="px-3 py-2 text-left">School two</td>
                <td className="px-3 py-2 text-left">Position</td>
                <td className="px-3 py-2 text-left">Qualification</td>
              </tr>
            </thead>
            <tbody>
              {this.FilteredData() === null ? (
                <tr>
                  <td
                    colSpan={7}
                    className="text-center p-5 bg-gray-100 rounded-md text-lg"
                  >
                    Loading...
                  </td>
                </tr>
              ) : this.FilteredData()!.length === 0 ? (
                <tr>
                  <td
                    colSpan={7}
                    className="text-center p-5 bg-gray-100 rounded-md text-lg"
                  >
                    No data found!
                  </td>
                </tr>
              ) : (
                this.FilteredData()!.map((item, i) => (
                  <tr>
                    <td>{i + 1}</td>
                    <td>
                      {this.FindUser(item.user_one, item.usersInfo)?.full_name}
                    </td>
                    <td>
                      {
                        this.FindSchool(item.school_one, item.schools)
                          ?.school_name
                      }
                    </td>
                    <td>
                      {this.FindUser(item.user_two, item.usersInfo)?.full_name}
                    </td>
                    <td>
                      {
                        this.FindSchool(item.school_two, item.schools)
                          ?.school_name
                      }
                    </td>
                    <td>{item.position_name}</td>
                    <td>{item.qualification_name}</td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </DashboardContainer>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  permutations,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  permutations: PermutationsStore;
} => {
  return {
    auth: auth,
    basic: basic,
    permutations: permutations,
  };
};

export const PermutationsReports = connect(mapStateToProps, {
  FC_GetPermutationsByDistrict,
  FC_GetNationalPermutations,
})(_PermutationsReports);
